@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed&display=swap);
:root {
  --topmenu-shadow: 0 4px 6px rgba(50, 50, 3, 0.1), 0 1px 3px rgba(0, 0, 0, 0.8);
  --shadow: 0 4px 6px rgba(50, 50, 3, 0.1), 0 1px 1px rgba(0, 0, 0, 0.8);
  --border: 1px solid rgba(0, 0, 0, 0.2);
  --menu-background-color: #fff;
  --menu-hover-background-color: #c1c1c1;
}

.nav {
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  height: 60px;
  top: 0px;
  align-items: center;
  display: flex;
  justify-content: center;
  justify-content: space-evenly;
  background-color: #212121;
  box-shadow: 0 4px 6px rgba(50, 50, 3, 0.1), 0 1px 3px rgba(0, 0, 0, 0.8);
  box-shadow: var(--topmenu-shadow);
  border: 1px solid rgba(0, 0, 0, 0.2);
  border: var(--border);
  z-index: 100;
}

.nav .nav_menu {
  width: 100%;
  text-align: center;
  justify-content: center;
  text-decoration: none;
  color: white;
}

.nav .nav_menu_side {
  display: flex;
  width: 100%;
  text-align: center;
  justify-content: space-evenly;
  text-decoration: none;
  color: white;
  flex-direction: row;
  font-size: 13px;
}

.nav .nav_menu .fa-film {
  display: flex;
  flex-direction: column;
  font-size: 40px;
}

.nav .nav_menu .fa-film .fas_name {
  font-size: 12px;
}

.nav .nav_menu:hover {
  display: flex;
  align-items: center;
  height: 100%;
  border-bottom: 3px solid white;
}

.nav .nav_menu_side_item:hover {
  cursor: pointer;
}

.nav .icon:hover {
  border-bottom: none;
}

.movies {
  display: flex;

  flex-wrap: wrap;
  margin-left: 40px;
  margin-right: 40px;
  justify-content: center;
}

.movie {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 20px;
}

.box{
  display: flex;
  margin-top: -45px;
  justify-content: flex-start;
  flex-wrap: nowrap;
  overflow:hidden; 
  scroll-behavior: smooth; 
}


.movie_rank {
  position: relative;
  color: white;
  font-size: 30px;
  top: 40px;
  left: 15px;
  font-family: "Sansita Swashed", cursive;
  text-shadow: 0 4px 6px rgba(19, 19, 0, 0.1), 2px 5px 3px rgba(0, 0, 0, 0.8);
}

.movie_link {
  text-decoration: none;
}

.movie_main_title {
  color: black;
  font-size: 13px;
  text-align: center;
}

.movie_title {
  font-size: 17px;
  line-height: 10px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #F0F2F0;
  height: 100%;
}

html,
body,
#root,
.App {
  height: 100%;
  display: flex;
  height: auto;
  flex-direction: column;
  align-content: center;
}

.loading {
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  color: black;
}

.about {
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  color: black;
}

.home {
  display: flex;
  justify-content: center;
  align-content: center;
  border-radius: 10px;
  height: 90vh;
}

.home_menu_title {
  align-content: center;
  font-size: 25px;
  font-weight: bolder;
  color: black;
  height: auto;
  margin-bottom: 80px;
  border-bottom: 3px solid black;
}


.addmovie {
  position: absolute;
  top: 21%;
  left: 75%;
  color: black;
  text-decoration: none;
  font-size: 13px;
}


.Boxoffice {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 20px;
  margin-right: 20px;
}

.Boxoffice .movie_rank {
  position: relative;
  color: white;
  font-size: 30px;
  top: 40px;
  left: 15px;
  font-family: "Sansita Swashed", cursive;
  text-shadow: 0 4px 6px rgba(19, 19, 0, 0.1), 2px 5px 3px rgba(0, 0, 0, 0.8);
}

.Boxoffice .movie_link {
  text-decoration: none;
}

.Boxoffice .Boxoffice_menu {
  display: flex;
  margin-top: 5px;
  width: 100%;
  height: 45px;
}

.Boxoffice .good {
  font-size: 13px;
  width: 30%;
  height: 100%;
  border: 1px solid rgb(139, 135, 135);
  border-radius: 5px;
  background-color: rgb(26, 27, 27);
}

.Boxoffice .good .far {
  color: white;
  width: 100%;
  margin-top: 14px;
  margin-left: 10%;
}

.Boxoffice .Boxoffice_menu a {
  margin-left: 10px;
  width: 65%;
  height: 100%;
  border-radius: 5px;
  text-align: center;
  vertical-align: bottom;
  color: white;
  line-height: 40px;
  background-color: rgb(14, 89, 150);
  text-decoration: none;
}

/* .Boxoffice:hover{
  background-color: chocolate;
} */

.ticketing {
  padding: 20px;
  height: 90.9%;

}

.ticketing_topmenu {
  display: flex;
  flex-direction: row;
  padding: 15px;
  justify-content: space-evenly;
  background-color: white;
  border-radius: 3px;
}

.ticketing_topmenu .ticketing_movie {
  width: 100%;
  background-color: white;
  color: black;
}

.own {
  border-right: rgb(138, 124, 124) 1px solid;
}

.ticketing_movie .ticketingmovie {
  width: 100%;
  background-color: rgb(20, 2, 2);
  padding: 15px;
  text-align: center;
  color: white;
  align-items: center;
  cursor: pointer;
}

.ticketing_movie .ticketingmovie .fa-calendar-alt {
  color: white;
  cursor: pointer;
  margin-left: 10px;
}

.ticketingmovie_poster {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  height: 490px;
}

.Ticketinposter {
  display: flex;
  align-items: ce;
  text-decoration: none;
  margin: 5px;
  padding: 5px;
}

.Ticketinposter:hover {
  background-color: #a19999;
  color: white;
  border-radius: 5px;
}

.Ticketinposter h4 {
  position: relative;
  top: -10px;
  margin-left: 10px;
  color: black;
}

.ticketingmovie_calendar {
  display: flex;
  flex-direction: column;

  justify-content: center;
}

.ticketing_movie button {
  align-items: center;
  background-color: white;
  border: none;
  position: relative;
}

/*캘린더*/
.rc-calendar {
  position: relative;
  font-size: 19px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-left: 10px;
  align-items: center;
  border-radius: 5px;
  width: 98%;
  box-shadow: var(--topmenu-shadow);
  border: var(--border);
  outline: 0;
  background-color: white;
  color: black;
}

.rc-calendar-input-wrap,
.rc-calendar-input {
  text-align: center;
  border: 0;
  outline: 0;
}

.rc-calendar-column-header {
  padding: 15px;
}

.rc-calendar-header {
  line-height: 30px;
  height: 30px;
  border-bottom: 1px solid rgb(207, 198, 198);
  border-top: 1px solid rgb(207, 198, 198);
}

.rc-calendar-body {
  margin: 10px;
  margin-top: 10px;
}

.rc-calendar-month-panel-body {
  display: none;
  flex-direction: column;
  border-top: 1px solid rgb(207, 198, 198);
}

.rc-calendar-my-select {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.rc-calendar-date-panel {
  text-align: center;
  border: 0;
  outline: 0;
}

.rc-calendar-date {
  background-color: white;
  width: 100%;
}

.rc-calendar-date:hover {
  border-radius: 60px;
  background-color: rgb(131, 204, 238);
}

.rc-calendar-footer {
  margin-bottom: 20px;
}

.rc-calendar-today-btn {
  padding: 4px;
  border-radius: 5px;
  color: white;
  background-color: rgb(9, 74, 148);
}

.rc-calendar-month-panel-header,
.rc-calendar-year-panel-body,
.rc-calendar-year-panel-header {
  display: none;
}

.rc-calendar-date,
.rc-calendar-month-panel-month,
.rc-calendar-footer-btn {
  cursor: pointer;
}

.ticketingmovie_time {
  position: relative;
  left: 10px;
  width: 98%;
  overflow-y: scroll;
  height: 230px;
}

.ticketingmovie_time {
  font-weight: 700;
}

.twoD {
  position: relative;
  font-size: 12px;
  font-weight: 500;
  top: 8px;
}

.ticketingmovie_time_info ._time_info_1 {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 60px;
  margin-top: 5px;
  text-align: center;
  align-items: center;
}

.ticketingmovie_time_info ._time_info_1 div {
  display: flex;
  flex-direction: column;
  width: 80px;
  margin-top: 5px;
  border: 1px solid rgb(161, 152, 152);
  border-radius: 3px;
  cursor: pointer;
  padding: 3px;
  margin-left: 8px;
}

.ticketingmovie_time_info ._time_info_1 div:hover {
  border-radius: 60px;
  background-color: #a19999;
}

.ticketingmovie_time_info ._time_info_1 span {
  font-size: 12px;
  font-weight: 500;
}

.movie_ticket {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-around;
  font-size: 15px;
  left: 15px;
  width: 70%;
  height: 200px;
  margin-top: 20px;
  border: 1px solid rgb(116, 117, 105);
  border-radius: 3px;
  text-align: center;
  font-weight: 600;
}

.movie_ticket span {
  font-size: 20px;
  font-weight: 700;
}

@media (max-width: 600px) {
  .ticketing {
    width: 70%;
  }

  .ticketing_topmenu {
    background: none;
  }

  .ticketing .ticketing_movie {
    width: 60%;
  }

  .ticketing_movie .ticketingmovie {
    font-size: 10px;
  }

  .fa-calendar-alt {
    display: none;
  }

  .ticketingmovie {
    cursor: pointer;
  }

  .ticketing_movie button {
    background: none;
  }

  .rc-calendar {
    position: relative;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin-left: 0px;
    align-items: center;
    border-radius: 5px;
    box-shadow: var(--topmenu-shadow);
    border: var(--border);
    outline: 0;
    background-color: white;
    color: black;
  }

  .rc-calendar-column-header {
    padding: 2px;
  }

  .rc-calendar-body {
    padding: 0;
    margin: 10px;
  }
  .ticketingmovie_time {
    left: 3px;
  }

  .movie_ticket {
    left: 0px;
  }

  .Ticketinposter h4 {
    font-size: 10px;
  }

  .ticketingmovie_time_info {
    width: 80%;
  }

  .ticketingmovie_time_info ._time_info_1 {
    display: flex;
    width: 80%;
    height: 60px;
    margin-top: 5px;
    text-align: center;
    align-items: center;
    font-size: 13px;
  }

  .ticketingmovie_time_info ._time_info_1 div {
    display: flex;
    flex-direction: column;
    width: 80px;
    margin-top: 5px;
    border: 1px solid rgb(161, 152, 152);
    border-radius: 3px;
    cursor: pointer;
    padding: 3px;
    margin-left: 5px;
  }

  .ticketingmovie_time_info ._time_info_1 div:hover {
    border-radius: 60px;
    background-color: #a19999;
  }

  .ticketingmovie_time_info ._time_info_1 span {
    font-size: 10px;
    font-weight: 500;
  }
}

::-webkit-scrollbar {
  width: 8px;
  background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background-color: #353b48;
}

.detail{
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.movie_data {
  display: flex;
  width: 70%;
  height: 400px;
  background-color: white;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movie_data_info {
  display: flex;
  flex-direction: column;
}
.movie_data img {
  position: relative;
  top: -50px;
  width: 200px;
  height: 300px;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie_data_title {
  margin-top: 20px;
  height: 50px;
  margin-bottom: 10px;
}

.data_box {
  padding: 10px;
  display: flex;
  border-top: 1px solid gray;
}

.data_box ul {
  position: relative;
  list-style: none;
  left: -30px;
  top: -13px;
  font-size: 13px;
}

.movie_data_summary {
  overflow-y: scroll;
}

html {
  scroll-behavior: smooth;
}

