.inner-star::before{
  color: gold;
}
.outer-star {
  position: relative;
  display: inline-block;
  color: #CCCCCC;
}
.inner-star {
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  overflow: hidden;
  white-space: nowrap;
}

.outer-star::before, .inner-star::before {
  content: '\f005 \f005 \f005 \f005 \f005';
  font-family: 'Font Awesome 5 free';
  font-weight: 900;
}



@media (max-width: 600px) {
  .movie_data {
    display: flex;
    margin-top: 140px;
    width: 80%;
    background-color: white;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 0px;
    overflow-y: scroll;
    flex-direction: column;
  }

  .movie_data img {
    position: relative;
    top: 10px;
    left: 20%;
  }
}

